import "./css/styles.css";
import React, { useState, useEffect } from "react";
import Home from "./components/home";
import About from "./components/about";
import Footer from "./components/footer";

function App() {
  const [showGoTop, setShowGoTop] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", function () {
      const pxShow = 900;
      if (window.scrollY >= pxShow) {
        setShowGoTop(true);
      } else {
        setShowGoTop(false);
      }
    });
  }, []);

  var instagram = "https://www.instagram.com/samaneh.photos";
  var linkedin = "https://www.linkedin.com/in/sam-rajabi";
  var git = "https://github.com/s-rajabi1991";
  var facebook = "https://www.facebook.com/samaneh.rajabi.12";

  return (
    <div className="App">
      <Home
        instagram={instagram}
        linkedin={linkedin}
        git={git}
        facebook={facebook}
      />
      <About />
      <Footer
        instagram={instagram}
        linkedin={linkedin}
        git={git}
        facebook={facebook}
        showGoTop={showGoTop}
      />
    </div>
  );
}
export default App;
