import "../css/home.css";
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faInstagram,
  faFacebook,
  faLinkedin,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";
import { faArrowCircleDown } from "@fortawesome/free-solid-svg-icons";
import myphoto from "../images/portrait.jpg";
import { Link } from "react-scroll";

function Home({ git, linkedin, instagram, facebook }) {
  return (
    <>
      <section id="home" className="s-home">
        <div className="s-home__bg" />
        <div className="row s-home__content">
          <div className="column">
            <img src={myphoto} alt="samaneh" />
            <h1>I'm Samaneh</h1>
            <h3>
              I'm a Calgary based software developer with nearly five years of
              professional coding experience. I love what I do and get excited
              about building innovative solutions that make our lives easier and
              more enjoyable. More{" "}
              <Link to="about" spy={true} smooth={true}>
                {" "}
                about me
              </Link>
              .
            </h3>
            <div className="s-home__content-social">
              <a href={git} target="_blank" rel="noreferrer">
                <FontAwesomeIcon icon={faGithub} />
              </a>
              <a href={linkedin} target="_blank" rel="noreferrer">
                <FontAwesomeIcon icon={faLinkedin} />
              </a>
              <a href={facebook} target="_blank" rel="noreferrer">
                <FontAwesomeIcon icon={faFacebook} />
              </a>
              <a href={instagram} target="_blank" rel="noreferrer">
                <FontAwesomeIcon icon={faInstagram} />
              </a>
            </div>
          </div>
        </div>
        <div className="s-home__scroll">
          <Link
            to="about"
            spy={true}
            smooth={true}
            className="s-home__scroll-link"
          >
            <span className="scroll-arrow">
              <FontAwesomeIcon icon={faArrowCircleDown} />
            </span>
            <span className="scroll-text">Scroll Down</span>
          </Link>
        </div>
      </section>
    </>
  );
}

export default Home;
