import "../css/about.css";
import React from "react";
import myresume from "../resume/Resume.pdf";
import myphoto from "../images/portrait.jpg";

function About() {
  return (
    <>
      <section id="about" className="s-about">
        <div className="row">
          <div className="column large-3 tab-12">
            <img className="s-about__pic" src={myphoto} alt="samaneh" />
          </div>
          <div className="column large-9 tab-12 s-about__content">
            <h3>About Me</h3>
            <p>
              Originally from Tehran,Iran, I have been living and working in
              Calgary, Canada since 2017. Having a B.Sc. in Software Engineering
              and a M.Sc. in IT Engineering from top universities in Iran, I am
              currently a Full Stack Web Developer at{" "}
              <a href="https://www.oplii.com/" target="_blank" rel="noreferrer">
                Oplii
              </a>
              , working with .NET, JavaScript, Microsoft Azure, and SQL. In my
              free time, I enjoy practicing yoga, spending time outdoors, and
              binging TV shows. I am also an avid boardgame player. Feel free to{" "}
              <a href="mailto:sam.rajabi01@gmail.com">send me an email</a> for
              further information about my skills and experience.
            </p>
            <hr />
            <div className="row s-about__content-bottom">
              <div className="column w-1000-stack">
                <a
                  href={myresume}
                  download="Samaneh Rajabi Resume"
                  className="btn btn--download"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width={24}
                    height={24}
                    viewBox="0 0 24 24"
                    style={{
                      fill: "rgba(0, 0, 0, 1)",
                      transform: "",
                      msFilter: "",
                    }}
                  >
                    <path d="M12 16L16 11 13 11 13 4 11 4 11 11 8 11z" />
                    <path d="M20,18H4v-7H2v7c0,1.103,0.897,2,2,2h16c1.103,0,2-0.897,2-2v-7h-2V18z" />
                  </svg>
                  Download Resume
                </a>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default About;
